@import  "../components/variables";

#content-side-panel-1{
	// background: @color-panel-bg;
	background-image:url('../images/opacity-70.png');
	margin-top: 2px;
	margin-top: -46px;

	> header{
		background: @grey5;
		color: @white;
		font-weight: bold;
		height: 46px;

		h2{
			padding: 16px 30px 0 30px;
			a{
				color: @white;
			}
		}

		.toggle{
			color: @white;
			font-size: 26px;
			padding: 10px 14px 10px 14px;
			max-height: 46px;
			//border-left: 2px solid @black;
			border-right: 2px solid @black;
			margin-right: -2px;
			display: block;
			float: right;

			.active-right &{
				margin-right: 0;
			}
		}
	}
	

	
	#panel-control-1{
		background: @color-panel-header;
		height: 45px;
		color: @color-panel-text;
		padding: 15px 0 0 30px;
		font-weight: bold;
		cursor: pointer;
	}

	section{
		margin: 10px;

		&.top{
			padding-top: 5px;
		}
		
		h1,h2,h3{
			padding: 0 10px 15px 10px;
			font-weight: bold;
			color: @color-panel-header;
			font-size: 16px;
		}
		
		h3{
			color: @color-panel-link;
		}

		a{
			color: @color-panel-link;
		}

		.active{
			border: 1px solid @color-panel-active-header-border;
			background: @color-panel-active-header-bg;
			padding: 10px 19px;
			margin-bottom: 10px;
			border-radius: 2px;
			color: #aeb5bd;
		}

		ul{
			padding-bottom: 7px;
			margin-bottom: 13px;
			border-bottom: 1px solid @color-panel-border;
			color: @color-panel-text;
			
			
			li{
				padding: 0 10px 8px 10px;
			
				strong{
					font-size: 16px;
				}
			}
		}
		ul.sports.more{
			margin:-17px -10px 10px -10px;
			padding:14px 10px 8px 10px;
			background: inherit;
			transition-delay:0.2s;
			border-bottom:none;
			li{
				a{
					i{
						position:absolute;
						right:20px;
					}
				}
			}
			ul.all-sports{
				height:0px;
				width: 100%;
				top: 44px;
				padding-top: 0px;
				left: 0;
				padding-bottom:0;
				background: #2677a6;
				position: absolute;
				overflow:hidden;
				-webkit-transition: all 0.2s ease-in;
				-moz-transition: all 0.2s ease-in;
				-o-transition: all 0.2s ease-in;
				transition: all 0.2s ease-in;
			  li{
					a{
						color: #ffffff;
						padding: 2px 5px;
						display: block;
						font-weight: bold;
						&:hover{
							color: #ffffff;
						}
					}
				}
			}
			&:hover{
				background: #2677a6;
				transition-delay:0s;
				ul.all-sports{
					// border-top:1px solid rgba(255, 255, 255, 0.1);
					height: 100%;
					padding-top: 12px;
					
				}
			}

			
		}
		.in-play{

			li{
				display: table;
				width: 100%;

				div{
					display: table-row;
				}
			
				span{
					display: table-cell;
					padding: 3px 0
				}

				.name{
					width: 90%;
				}

				.score{
					min-width: 10%;
					background: @color-panel-score;
					text-align: center;
					color: @white;
					font-weight: bold;
					padding: 0 9px;

					&.home{
						padding-top: 5px;
					}

					&.away{
						padding-bottom: 5px;
					}
				}
			}
		}
	}
}

#content-side-panel-2{
	section{
		padding: 10px;
		min-height: 150px;

		header{
			padding: 10px 14px 20px 14px;
			h2{
				font-size: 22px;
				font-weight: bold;
			}
			h3{
				color: @color-link;
			}
		}

		ol{
			padding-left: 14px;
			font-size: 12px;
			font-weight:  bold;
			li{
				padding: 0 0 18px;
				a{
					padding-left: 8px;
				}
			}
		}
	}
}



@media all and (max-width: 1024px) {
	#content-side-panel-2{
	top:93px !important;
		#bet-slip {
			margin-top: -1px;
		}	
	} 
}

@media all and (max-width: 960px) {

	
	.container-main {
	
		#content-side-panel-1{
		margin-top:0;
		width:175px;
		section{
			ul.sports.more{
				margin: -15px -10px 10px -10px;
			}
		}

	}

		#content-side-panel-2{ 

			position: absolute;
			// border-left: 2px solid #252525;
			box-shadow: 0px 0px 14px #000;
			width:0px;
			overflow:hidden;
			opacity:0;
			filter: alpha(opacity=00); /* For IE8 and earlier */
			top: 127px;
			
			> div {
				min-width:300px;
				width:auto;
			}
			
			&.show {
				width:300px;
				opacity:1;
				filter: alpha(opacity=100); /* For IE8 and earlier */
			}
			
			#content-side-panel-bets {
				background-image: url('../images/opacity-70.png');
			}
			#bet-slip .bet-slip-content {
				margin-top: 0px;
			}
			
			
			
			.inplay-panel{
					width:300px;
					.progressBar, .teams, .msg, .pitch{
						width:280px;
					}
				}
				.tab-bar{
					height:40px;
					padding-top:10px;
					background-color: #1F1F1F;
					ul{
					
						li{
							width:125px;
							height:40px;
							padding-top:5px;
							background: #2f2f2f;
							&:not(.active){
								border-top:4px solid transparent;
								background:#3A3A3A;
							}
							&:first-child{
								width:50px;
								float:left;
								box-shadow:none;
								background:#2F2F2F;
								background-image: none;
								&:hover{
								cursor: pointer;
									i{
										color:@color-secondary;
									}
								}
							}
							&.active{
								margin-top:-4px;	
								height: 40px;
								padding-top: 8px;
								border-top:4px solid @color-secondary;
								
						


								a{
										color:white;
								}
							}
			
							a{
								color:#bdbdbd;
								font-weight:bold;
							}
							
							
						}
					}
				}

		}
	
	}
	

}
@media all and (max-width: 480px) {
	
	.container-main {
		#content-side-panel-2{ 
			top: 106px;
			> div {
				min-width:300px;
				width:300px;
			}
				
		}
	}


}



