@import "mixins";
@import "variables";

.container-main{
	.clearfix;
	.transition-ease-in;
	width: @width-container-main;
	clear: both;
	
	#content-main{
		.clearfix;
		/*
width: @width-content-main;
		&:extend(.float-left);	
		margin-top: @width-content-margin;
		min-height: 150px;
*/
		-webkit-transition: all 0.5s ease-in-out;
		 -moz-transition: all 0.5s ease-in-out;
		 -o-transition: all 0.5s ease-in-out;
		 transition: all 0.5s ease-in-out;
		
		margin-top: @wrapper-page-padding-px;
		position: relative;
		// margin-left: 210px;
	// 	margin-right: 330px;
		
		margin-left: @width-side-panel-1-px + (@wrapper-page-padding-px);
		margin-right:@width-side-panel-2-px + (@wrapper-page-padding-px);

		width: auto;
		
		section{
			background: @white;
			padding: @wrapper-page-padding-px;
			margin-bottom: @wrapper-page-padding-px;
		}

	}

	.active-right &{
		margin-left: @width-margin-left-offset;
		#content-main{
			width: @width-content-main-reduced;
			section{
				padding: @width-content-main-reduced-padding;
			}
		}
	}

}

#content-side-panel-1{
	width: 200px;
	min-height: 300px;
	&:extend(.float-left);
	margin-right: @width-content-margin;
	padding-bottom: 7px;
	margin-bottom: 13px;
	border-bottom: 1px solid #4F4F4F;
	color: #FFF;
	position: absolute;
	-webkit-transition: all 0.5s ease-in-out;
	-moz-transition: all 0.5s ease-in-out;
	-o-transition: all 0.5s ease-in-out;
	transition: all 0.5s ease-in-out;  
}

#content-side-panel-2{
	margin-top: @wrapper-page-padding-px;
	&:extend(.float-right);
	width: 320px;
	min-height: 150px;
	position: absolute;
	top: 51px;
	right: 10px;
	
	section{
		margin-bottom: 10px;
	}
}

#content-side-panel {
	 -webkit-transition: all 0.4s ease-in-out;
	 -moz-transition: all 0.4s ease-in-out;
	 -o-transition: all 0.4s ease-in-out;
	 transition: all 0.4s ease-in-out;
}


.menuToggle,
.betslipToggle {
	display:none;
}

@media all and (max-width: 1124px) {
	
	.container-main {
		#content-main{
			section{
				padding: 10px;
			}
		}
	}

}

@media all and (max-width: 960px) {
	.container-main {
	
		#content-main {
			margin-right: 0;
			margin-left: @width-side-panel-3-px + (@wrapper-page-padding-px);
		}
		
	}
	
	.betslipToggle {
		display:block;
	}

}
@media all and (max-width: 648px) {

	
	.container-main {
	
		#content-main {
			margin-right: 0;
			margin-left: 0;
		}	
		
		
		#content-side-panel {
			margin-top: 10px;
			background-image: url('../images/opacity-70.png');
			width: 0px;
			height: 100%;
			position: absolute;
			z-index:20;
			opacity:0;
			filter: alpha(opacity=00); /* For IE8 and earlier */
			
			> div {
				width:200px;
			}
			
			&.show {
				width: 200px;
				opacity: 1;
				filter: alpha(opacity=100); /* For IE8 and earlier */

			}
		}
		
		
	}
	
	.menuToggle {
		display:block;
	}
	
}



