@import  "../components/variables";
@import  "../components/mixins";

.competitions{

	header{
		
		padding: 4px 0px;
		font-weight: bold;
		position: relative;
		border:0;
		

		.secondary{
			position: absolute;
			right: 2px;
			font-size: 0.8em;
			bottom: 10px;
		}
	}
	
	> header {
		padding-bottom:0;
	}
	
	> ul{
		> li{
			
			header{
				.header-internal-list();
			}

			ul{
				width: 100%;
				background: @white;
				.clearfix();
				margin-bottom: 15px;
				font-size: 0.9em;

				li{
					display: block;
					//width: 65px;
					width: 33.333333333333%;
					float: left;
					text-align: left;
					padding: 3px 3px 0 0;

					.col &{
						width: 100%;
					}

					.col.double &{
						width: 50%;
					}


					a{
						font-weight: bold;
						background: @grey11;
						display: block;
						padding: 8px 10px 10px 10px;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;

						&:hover{
							text-decoration: none;
							background: darken(@grey11, 5%);
						}
					}
				}
			}
		}
	}

}

@media all and (max-width: 480px) {
	.col.double .competitions > ul > li ul li {	
		width: 100%;
	}

}