

@font-face {
    font-family: 'open_sanscondensed_light';
    src: url('../../fonts/OpenSans-CondLight-webfont.eot');
    src: url('../../fonts/OpenSans-CondLight-webfont.eot?#iefix') format('embedded-opentype'),
         url('../../fonts/OpenSans-CondLight-webfont.woff') format('woff'),
         url('../../fonts/OpenSans-CondLight-webfont.ttf') format('truetype'),
         url('../../fonts/OpenSans-CondLight-webfont.svg#open_sanscondensed_light') format('svg');
    font-weight: normal;
    font-style: normal;

}


@font-face {
    font-family: 'open_sans_condensedbold';
    src: url('../../fonts/OpenSans-CondBold-webfont.eot');
    src: url('../../fonts/OpenSans-CondBold-webfont.eot?#iefix') format('embedded-opentype'),
         url('../../fonts/OpenSans-CondBold-webfont.woff') format('woff'),
         url('../../fonts/OpenSans-CondBold-webfont.ttf') format('truetype'),
         url('../../fonts/OpenSans-CondBold-webfont.svg#open_sans_condensedbold') format('svg');
    font-weight: normal;
    font-style: normal;

}


.inplay-panel {
	
	min-height: 234px;
	width: 320px;
	background: rgb(73,73,73); /* Old browsers */
	background: -moz-radial-gradient(center, ellipse cover,  rgba(73,73,73,1) 9%, rgba(47,47,47,1) 71%); /* FF3.6+ */
	background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(9%,rgba(73,73,73,1)), color-stop(71%,rgba(47,47,47,1))); /* Chrome,Safari4+ */
	background: -webkit-radial-gradient(center, ellipse cover,  rgba(73,73,73,1) 9%,rgba(47,47,47,1) 71%); /* Chrome10+,Safari5.1+ */
	background: -o-radial-gradient(center, ellipse cover,  rgba(73,73,73,1) 9%,rgba(47,47,47,1) 71%); /* Opera 12+ */
	background: -ms-radial-gradient(center, ellipse cover,  rgba(73,73,73,1) 9%,rgba(47,47,47,1) 71%); /* IE10+ */
	background: radial-gradient(ellipse at center,  rgba(73,73,73,1) 9%,rgba(47,47,47,1) 71%); /* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#494949', endColorstr='#2f2f2f',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
	padding-top: 9px;
	margin: auto;
	position:relative;
	
	.progressBar {
		height: 10px;
		width: 300px;
		background-color: #1F2327;
		margin: auto;
		box-shadow: inset 0px 0px 6px rgba(0, 0, 0, 0.65);
		border-radius: 2px;
		clear: both;
		position: relative;
		margin-bottom: 9px;
		
		.time {
			height: 20px;
			width: 60px;
			border: 1px solid #202325;
			position: absolute;
			left: 50%;
			margin-left: -28px;
			border-radius: 4px;
			z-index: 10;
			margin-top: -15px;
			box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.5);
			color: #fff;
			background: #4C4C4C;
			background: -moz-linear-gradient(top, #4c4c4c 0%, #3b3c3f 100%);
			background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #4C4C4C), color-stop(100%, #3B3C3F));
			background: -webkit-linear-gradient(top, #4C4C4C 0%, #3B3C3F 100%);
			background: -o-linear-gradient(top, #4c4c4c 0%, #3b3c3f 100%);
			background: -ms-linear-gradient(top, #4c4c4c 0%, #3b3c3f 100%);
			background: linear-gradient(to bottom, #4C4C4C 0%, #3B3C3F 100%);
			filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#4c4c4c', endColorstr='#3b3c3f', GradientType=0);

			
			i {
				float: left;
				margin-top: 3px;
				margin-left: 6px;
			}
			
			span {
				display: block;
				float: right;
				text-align: center;
				font-size: 13px;
				text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.27);
				font-weight: 800;
				margin-top: 2px;
				margin-right: 4px;
			}
		}
		
		.activeBar {
			background: rgb(96,130,41); /* Old browsers */
			background: -moz-linear-gradient(left,  rgba(96,130,41,1) 0%, rgba(116,160,47,1) 100%); /* FF3.6+ */
			background: -webkit-gradient(linear, left top, right top, color-stop(0%,rgba(96,130,41,1)), color-stop(100%,rgba(116,160,47,1))); /* Chrome,Safari4+ */
			background: -webkit-linear-gradient(left,  rgba(96,130,41,1) 0%,rgba(116,160,47,1) 100%); /* Chrome10+,Safari5.1+ */
			background: -o-linear-gradient(left,  rgba(96,130,41,1) 0%,rgba(116,160,47,1) 100%); /* Opera 11.10+ */
			background: -ms-linear-gradient(left,  rgba(96,130,41,1) 0%,rgba(116,160,47,1) 100%); /* IE10+ */
			background: linear-gradient(to right,  rgba(96,130,41,1) 0%,rgba(116,160,47,1) 100%); /* W3C */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#608229', endColorstr='#74a02f',GradientType=1 ); /* IE6-9 */
			position: absolute;
			left:0;
			height:100%;
			width:75%;
			box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 0.22);
			border-radius: 2px 0px 0px 2px;
		}
	}
	
	
	.teams {
	
			height: 46px;
			width: 300px;
			margin: auto;
		
		.team {
			
			float:left;
			width:50%;
			text-align: right;
			color:#fff;
			font-size: 26px;
			position:relative;
			
			
			.loc {
				font-size: 11px;
				color: #8B8E8F;
				float: right;
				font-weight: 600;
			}
			
			.name {
				float:right;
				margin-top: 2px;
				clear:both;
				font-size: 20px;
				font-weight: 500;
			}
			.score {
				float: left;
				font-size: 36px;
				margin-left: 13px;
				margin-top: -9px;
				position: relative;
				z-index: 100;
				font-family: "HelveticaNeue-CondensedBold";
			}
			
			&:first-child {
				text-align: left;
				
				.loc {
					
					float: left;
				}

				
				.name {
					float:left;
				}
				.score {
					float:right;
					margin-right: 10px;
				}
				
				.flash {
					left:auto;
					right: -11px;
					top: -5px;
				}
			}
			
		}
	}
	
	.v {
		position: absolute;
		left: 50.07%;
		margin-top: 11px;
		font-size: 24px;
		color: #84888B;
		margin-left: -5px;
	}
	
	.msg {
		height: 23px;
		border-top: 1px solid #565B5E;
		padding-top: 3px;
		width: 300px;
		margin: auto;
		
		span {
			color: #CACDCE;
			font-family: "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
			font-weight: 700;
			font-size: 9px;
		}

	}
	
