
#main-inplay {
	pointer-events:none;
	 > div {
		 pointer-events:none;
	 }
}
.inplay-area {
	height: 367px;
	margin-bottom: 10px;
	background: transparent;
	margin-left: -10px;
	margin-bottom: -80px;
	margin-right: -60px;
	margin-top: -56px;
	padding-right: 60px;
	position: relative;
	z-index: -1;
	background-image:url('../images/inplay-assests/main-inplay-pitch.png');
	background-size: 100% 100%;
	padding-top: 55px;
	padding-right: 60px;
	padding-left: 10px;
	
	.inplay-box {
		height: 100px;
		width: 70%;
		margin: auto;
		background-image: url('../images/opacity-70.png');
		margin-top: 65px;
		box-shadow: 0px -2px 25px rgba(0, 0, 0, 0.94);
		border: 1px solid rgba(36, 36, 36, 0.24);
		border-top: 1px solid rgba(255, 255, 255, 0.13);
		z-index: 100;
		position: relative;
		pointer-events: auto;
		
		.progressBar {
			height: 10px;
			background-color: #008000;
			position: relative;
			background-color: #292F33;
			box-shadow: inset 0px 0px 10px #000;
			margin-left: -1px;
			margin-right: 1px;
			margin-top: -1px;
			border-top: 1px solid rgba(63, 63, 63, 0.45);
			width: 50%;
			float: left;

			
			.activeBar  {
				background: #608229;
				background: -moz-linear-gradient(left, #608229 0%, #74a02f 100%);
				background: -webkit-gradient(linear, left top, right top, color-stop(0%, #608229), color-stop(100%, #74A02F));
				background: -webkit-linear-gradient(left, #608229 0%, #74A02F 100%);
				background: -o-linear-gradient(left, #608229 0%, #74a02f 100%);
				background: -ms-linear-gradient(left, #608229 0%, #74a02f 100%);
				background: linear-gradient(to right, #608229 0%, #74A02F 100%);
				filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#608229', endColorstr='#74a02f', GradientType=1);
				position: absolute;
				left: 0;
				top:-1px;
				height: 100%;
				width: 75%;
				box-shadow: inset 0px 1px 0px rgba(255, 255, 255, 0.22);
			}
			
			.time {
				height: 24px;
				width: 76px;
				top: -7px;
				margin: auto;
				position: absolute;
				left: 100%;
				margin-left: -33px;
				z-index: 10;
				border-radius: 2px;
				box-shadow: inset 0px 1px 0px rgba(142, 149, 204, 0.25);
				border: 1px solid rgba(0, 0, 0, 0.27);
				border-bottom: 1px solid #000;
				border-top: 1px solid rgba(82, 82, 82, 0.4);
				background: #4C4C4C;
				background: -moz-linear-gradient(top, #4c4c4c 0%, #3b3c3f 100%);
				background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #4C4C4C), color-stop(100%, #3B3C3F));
				background: -webkit-linear-gradient(top, #4C4C4C 0%, #3B3C3F 100%);
				background: -o-linear-gradient(top, #4c4c4c 0%, #3b3c3f 100%);
				background: -ms-linear-gradient(top, #4c4c4c 0%, #3b3c3f 100%);
				background: linear-gradient(to bottom, #4C4C4C 0%, #3B3C3F 100%);
				filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#4c4c4c', endColorstr='#3b3c3f', GradientType=0);
				text-shadow: 0px 0px 8px rgba(0, 0, 0, 0.4);
				color: #FFF;
				font-weight: 600;
				padding-top: 3px;
				text-align: center;
				text-indent: -2px;
				font-size: 15px;

			}
				
		}
		
		.iconBar {
			width: 50%;
			float: left;
			height: 11px;
			margin-top: -1px;
			margin-bottom: -1px;
			background: rgb(76,76,76); /* Old browsers */
			background: -moz-linear-gradient(top,  rgba(76,76,76,1) 0%, rgba(59,60,63,1) 100%); /* FF3.6+ */
			background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(76,76,76,1)), color-stop(100%,rgba(59,60,63,1))); /* Chrome,Safari4+ */
			background: -webkit-linear-gradient(top,  rgba(76,76,76,1) 0%,rgba(59,60,63,1) 100%); /* Chrome10+,Safari5.1+ */
			background: -o-linear-gradient(top,  rgba(76,76,76,1) 0%,rgba(59,60,63,1) 100%); /* Opera 11.10+ */
			background: -ms-linear-gradient(top,  rgba(76,76,76,1) 0%,rgba(59,60,63,1) 100%); /* IE10+ */
			background: linear-gradient(to bottom,  rgba(76,76,76,1) 0%,rgba(59,60,63,1) 100%); /* W3C */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4c4c4c', endColorstr='#3b3c3f',GradientType=0 ); /* IE6-9 */
			box-shadow: inset 0px 1px 0px #575967;
			border-bottom: 1px solid #1E1E1E;
			display:table;
			margin-left: 1px;
			margin-right: -1px;
						
			.icons {
				
				display: table-row;
				
				.gap {
					display:table-cell;
					width:30%;
				}

				span.icon {
					display:table-cell;
					width:14%;
					position: relative;
					
					> div {
						background-repeat: no-repeat;
						background-position: center center;
						height:50px;
						width:50px;
						position: absolute;
						margin-left: -1px;
						margin-top: -21px;
						left: 50%;
						margin-left: -26px;
					}
					&.corner-flag div  {
						background-image:url('../images/inplay-assests/corner-flag-icon.png');
					}
					&.yellow-card div  {
						background-image:url('../images/inplay-assests/yellow-card-icon.png');
					}
					&.red-card div  {
						background-image:url('../images/inplay-assests/red-card-icon.png');
					}
					&.penalty div  {
						background-image:url('../images/inplay-assests/penalty-icon.png');
					}
					&.goal div  {
						background-image:url('../images/inplay-assests/goal-icon.png');
					}
				}
				
			}

		}
		
		.teams {
			
			display:table;
			width: 100%;
			margin-left: 1px;
			padding-top: 1px;
		
			.row {
				display: table-row;
	
				&.middle, &.bottom {
					height: 44px;
					
				}
				
				&.middle > * {
					border-bottom: 1px solid rgba(226, 226, 226, 0.1);
				}
				
				> * {
					display:table-cell;
					vertical-align: middle;
					
					
				}
				
				span.team {
					font-size: 20px;
					font-weight: 600;
					color: #FFF;
					text-shadow: 0px 0px 3px #000;
					padding-left: 15px;
					width:65%;
					
				}
				
				.stat {
					width:7%;
					font-size: 20px;
					font-weight: 600;
					color: #FFF;
					text-shadow: 0px 0px 3px #000;
					text-align: center;
					
					
					&.goal {
						background-color: rgba(209, 199, 129, 0.1);
						border-left: 1px solid rgba(37, 35, 21, 0.75);
						color: #FFDF39;
					}	
				}
			}
		
		}
	}
	
	.box-shadow {
		height:50px;
		width: 78%;
		margin: auto;
		clear:both;
		background-image:url('../images/inplay-assests/floating-shadow.png');
		background-size: 100% 100%;
		margin-top:10px;
	}
}

@media all and (max-width: 1200px) {
	
	.inplay-area {
		.inplay-box {
		
			width: 90%;
			
		}
		.box-shadow {
			width: 100%;
		}
		
	}

}

@media all and (max-width: 420px) {
	
	.inplay-area {
		
		height: 285px;
		
		.inplay-box {
			margin-top: 25px;
			width: 97%;
			height: 88px;
		
			.teams {
					.row {
					
					&.middle, &.bottom{
						height: 38px;
					}
				
						span.team {
							font-size: 16px;
							width: 60%;
							padding-left: 7px;
						}
						
						.stat {
							font-size: 16px;
						}
				}
			}
			
			.iconBar .icons .gap {
				width: 28%;
			}
			
			
			.timeBar {
				.clock {
					font-size: 13px;
					margin-left: -30px;
					width:60px;
					height:22px;
					top:-18px;
				}
			}

		}
		
		.box-shadow {
			margin-top: 0px;
			height: 30px;
		}
		
		
	}

}






