@import  "../components/variables";
@import  "../components/mixins";

.promos{
	
	margin-top: 20px;
	
	.promo-block{
		width: 32.83%;
		float: left;
		cursor: pointer;
		margin-right: 0.5%;

	}

	.promo-head{
		background: @grey7 url(../images/promo-bg-horses.png);
		height: 170px;
		position: relative;
		
		h2{
			background: @color-highlight;
			padding: 10px;
			display: inline-block;
			color: @white;
			font-size: 12px;
			font-weight: bold;
		}

		header{
			p{
				background: @grey2;
				padding: 10px;
				display: inline-block;
				color: @color-highlight;
				font-size: 12px;
				font-weight: bold;
			}
		}

		.promo-content{
			position: absolute;
			bottom: 0;
			width: 100%;
		}
		

	}

	.market{
		
		display: none;
		width: 100%;

		li{

			div{
				padding: 0;
				margin-bottom: 1px;
				.bgfade(@black, 0.7);
				color: @white;
				font-weight: bold;

				a{
					color: @white;
					font-weight: bold;
				}
			}

		}
	}

	article{
		padding: 10px;
		color: @grey2;

		h3{
			font-size: 16px;
			font-weight: bold;
			padding-bottom: 12px;
		}

		p{
			line-height: 1.4em;

			&.cta{
				padding-top: 20px;
				font-weight: bold;
				font-size: 0.9em;
			}
		}
	}

}

@media all and (max-width: 768px) {
	
	.slider-highlights.promos { 
		height:120px;
		overflow:hidden;
		section {
			
			&.promo-block{
				width: 100%;
				float:left;
			}
			
			.promo-head {
				height: 120px;
			}
		}
	
	}
	
}
