@import  "variables";
@import  "mixins";

@font-face {
  font-family: 'ico';
  src: url('data:application/octet-stream;base64,') format('woff'),
  url('data:application/octet-stream;base64,') format('truetype');
}




#header-main{
	color: white;
	background-size: 18%;
	i{
		margin-left: 2px;		
	}
		ul#menu {
			list-style-type:none;
			margin:0;
			padding:0;
			position: absolute;
			display:none;
		}

		ul#menu li {
			display:inline-block;
			float: left;
			border-bottom: 1px solid #5E5F5F;
		}
		ul#menu li a {
			display: block;
			/* min-width: 140px; */
			height: 30px;
			text-align: left;
			line-height: 30px;
			color: #fff;
			background: @grey6;
			text-decoration: none;
			font-size: 12px;
			padding-left: 20px;
			font-weight: bold;
		}

		ul#menu li:hover a {
			background: @grey6;
		}

		ul#menu li:hover ul a {
			background: #f3f3f3;
			color: #2f3036;
			height: 40px;
			line-height: 40px;
		}

		ul#menu li:hover ul a:hover {
			background: #19c589;
			color: #fff;
		}

		ul#menu li ul {
			display: none;
		}

		ul#menu li ul li {
			display: block;
			float: none;
		}
		
		ul#menu li ul li a {
			width: auto;
			min-width: 100px;
			padding: 0 20px;
		}

		ul#menu li a:hover + .hidden, .hidden:hover {
			display: block;
		}

		.show-menu {
			font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
			text-decoration: none;
			color: #fff;
			background-color: @color-nav-global-link;
			text-align: center;
			padding: 10px 0;
			display: none;
			font-size: 14px;
			width:45px;
			cursor:pointer;
		
			&:before { 
				content: '\e801';
				display: inline-block;
				width: 32px;
				color: #fff;
				font-family: "ico";
				font-style: normal;
				font-size: 22px;
				font-weight: normal;
				font-variant: normal;
				text-align: center;
				text-transform: none;
			}
		
	}

	input[type=checkbox]{
	    display: none;
	}
	input[type=checkbox]:checked ~ #menu{
	    display: block;
	}

	.header-content{
		position: relative;
	}

	h1{
		position: absolute;
		top: 19px;
		margin-left: 15px;
		height: 23px;
		width: 164px; 
		background-size: 164px 23px;
		background-repeat: no-repeat;
		display: block;
		background-image: url("@{image-path}/logo.gif");
		text-indent: -9999px;
		white-space: nowrap;
	}
	
	.header-image{
		display: none;
		width: 25%;
		min-height: 194px;
		position: absolute;
		top: -10px;
		left: 32.5%;
		text-indent: -99999px;
		background: url("@{image-path}/bg-racing.png") 50% 0 no-repeat;
	}

	.banner{
		padding-bottom: 40px;
	}

	.promo{
		min-height: 114px;
		padding: 0 0 0 60%;
		background: fade(@color-highlight, 30%);
		margin-bottom: 20px;
		display: none;

		img{
			display: none;
		}
	}

	&.show-promo{

		min-height: 188px;
		
		h1{
			top: 200px;
		}

		.header-image{
			display: block;
		}

		.promo{
			display: block;
			
			.remove{
				.pill();
				position: absolute;
				right: 15px;
				margin-top: 15px;
				padding-top: 0.438%;
				color: @color-link;
				&:after{
					font-family: fontAwesome;
					content: "\f00d";
				}
				span{
					display: inline-block;
					text-indent: -99999px;
				}
			}


			img{
				display: block;
			}
		}
	}

	.sign-in-cta{
		position:absolute;
		top: 4px;
		.btn-grey{
			.btn(@color-nav-main-bg, @color-link, @white);
			width: 80px;
			display: inline-block;
			margin: 0 5px;
			border: none;
			float: right;
		}
	
		.btn{
			.btn(@color-link, @color-link, @white);
			width: 80px;
			display: inline-block;
			margin: 0 5px;
			border: none;
			float: right;
		}

		.join{
			float: right;
		}

		a{
			color: @white;
			font-weight: bold;
		}
	}


	.nav-global{
		.clearfix;
		background: transparent;

		ul{
			.create-percentage-page-wrapper(700px);
			text-align: left;
			display: block;
			float: left;
			margin-left: @width-side-panel-1-px;
			max-height: 52px;
		//	overflow:hidden;

			> li{
				display: inline-block;
				width: auto;
				padding: 0 0 0px 0;
				font-size: 1.3em;
				line-height: 1.3em;
				font-weight: bold;

				a{
					color: @color-nav-global-link;
					padding: 12px 24px 16px 24px;
					display: inline-block;

					&:hover{
						text-decoration: none;
						background: @color-nav-global-link-hover;
						//border-top: 4px solid @color-highlight;
						//padding-bottom: 12px;
					}
					
					&.active{
						border-top: 4px solid @color-highlight;
						//padding-bottom: 12px;
						background-color: @color-nav-global-link-active;
					}

				}
			}
		}
	}

	.user{
		.create-percentage-page-wrapper(280px);
		padding: 10px 0 0 14px;
		position: absolute;
		right:6px;
		
		.details{
			.btn(@grey5, @grey5, @white);
			width: auto;
			line-height: 1.6em;
			text-align: left;
			margin-left: 15%;
			display: none;

			&:hover{
				background: @color-highlight;
				border-color: @color-highlight;
				color: @white;
			}
			
			&:after{
				content: '\f078';
				font-family: fontAwesome;
				float: right;
			}
		}

		&.logged-in{
			.details{
				display: block;
			}
			
			&:hover{
				.details{
					border-radius: 5px 5px 0 0;
					background: @color-highlight;
					border-color: @color-highlight;
					color: @white;
				}

				.user-account{
					display: block;
				}
			}

			.user-account{
				font-size: 12px;
				margin-top: 2px;
				position: absolute;
				width: 320px;
				top: 35px;
				right: 0px;
				// overflow: hidden;
				display: none;
				z-index: 9999; 
				background: @color-highlight;
				padding: 5px 0;
				transition-delay: 1s;
				li{
					margin: 0;
					padding: 0;
					display: block;
					width: 100%;
					text-align: left;

					&:first-child{
						border-top: none;
					}

					a{	
						font-weight: bold;
						color: @white;
						display: block;
						background: @color-highlight;
						padding: 5px 12px;

						&:hover{
							border: none;
							text-decoration: underline;
						}
					}
				}
			}
		}



		
	}

	.nav-all{
		display: none;
		padding: 20px 15%;
		background: @color-nav-all-bg;

		ul{
			width: 100%;
			margin: 0 auto;
			-webkit-column-count: 5;
	 		-webkit-column-gap: 10px;
	 		-moz-column-count: 5;
	 		-moz-column-gap: 10px;	

	 		li{
 				padding-bottom: 6px;
	 		}


	 		a{
	 			color: @color-nav-all-link;
	 			font-weight: bold;
	 		}
		}	
	}

	.nav-main {
		
		height: 46px;
		background: @color-nav-main-bg;
		.clearfix;
		padding-left: 200px;
		margin-right: 330px;
		.panel-control-1{
			&:after{
				content:".";
				visibility: hidden;
			}
		}

		.panel-control-1,
		.panel-control-2{
			float: left;
			//width: 22.3%;
			font-size: 26px;
			.transition-ease-in();

			a{
				display: inline-block;
				background: @color-nav-main-control;
				color: @color-nav-main-control-text;
				height: 45px;
				padding: 10px 14px 0 14px;
				border-right: 2px solid @black;
			}
		}

		.panel-control-2{
			float: right;
			width: 0%;
			background: red;
			a{
				float: right;
				border-left: 2px solid @black;
			}
		}

		.active-right &{
			.panel-control-1,
			.panel-control-2{
				//background: blue;
			}

			.panel-control-1{
				width: 0%;
			}

			.panel-control-2{
				width: 25.5%;
			}

			.nav-main-container{
				width: 74.5%;
			}

			.toggle{
				margin-left: 0;
			}
		}

		.nav-main-container{
			width: auto;
			//float: left;
			.clearfix();

			.toggle{
				height: 45px;
				width: 45px;
				background: @grey5;
				display: block;
				color: white;
				font-size: 1.6em;
				padding: 11px 0 0 12px;
				float: left;
				border-right: 2px solid @black;
				margin-left: -1px;


				&.bet-slip{
					float: right;
					border: none;
					border-left: 2px solid @black;
					position: relative;
					
					.active-right &{
						margin-right: -2px;
					}

					em{
						position: absolute;
						font-size: 0.5em;
						min-width: 20px;
						height: 20px;
						background: @color-highlight;
						border-radius: 9px;
						display: table-cell;
						vertical-align: middle;
						text-align: center;
						line-height: 20px;
						padding: 0 6px;
						top: 2px;
						right: 2px;
						font-weight: bold;
					}

				}
			}
		}

		ul{
			width: 36%;
			padding-left: 10px;
			// margin-left: @width-side-panel-1-px - 7;
			float: left;
			.transition-ease-in();
			&.date-drop-min{
					display:none;
					ul{
					padding: 5px 6px;

						li{
							float: none;
							padding:5px;
							a{
								
								padding:5px;
							}
						}
					}
			}
			&.nav-sport-min{
				display:none;
				
				}
			&.nav-sport{
				float: right;
				width: auto;
				padding-right: 0;
				max-width: 79%;
				text-align: right;
				max-height: 46px;
			
				li{
					a{
						border-left: 1px solid @color-nav-main-border;
					}
				}
			}

			> li{
				display: block;
				float: left;
				padding: 16px 0;

				a{
					color: @color-nav-main-link;
					
					padding: 0 10px;
					font-weight: bold;

					&:hover{
						color: @color-nav-main-link-hover;
					}

					&.active{
						color: @color-nav-main-link-active;
						background: @color-highlight;
						border-radius: 3px;
						border-right: none;
						padding: 4px 10px;
					}
				}

				&:first-child{
					a{
						border: none;
					}
				}
				
				&.more{
					position: relative;
					padding-left: 0px;
					padding-bottom: 20px;
					padding-right: 20px;
					a{
						padding:0;
						padding-left:20px;
					}
					&.highlight{
						padding-right: 0;
						margin-left: 0;
						margin-right: 8px;
						padding-left: 6px;

						a{
							border: none;
							
						}
					}

					&:hover{
						background: @grey6;
			
						
						&.highlight:hover{
							background: @color-highlight;
						}

						a{
							color: #FFFFFF;
						}
						ul{
							display: block;
							height: auto;
							li{
								width: 100%;
								display: block;
								color: white;
								border: none;
							}
							a{
								color: #FFFFFF;
							}
						}
					}

					ul{
						background: @color-nav-main-more-bg;
						text-align: left;
						position: absolute;
						width: 160px;
						padding: 5px 6px;
						margin-top: 18px;
						display: none;
						z-index: 99999;
						right: 0;
						
						li{
							padding:0;
							margin: 0;
							display: block;
							border-top: 1px solid @color-nav-main-more-link-border;
							

							&:first-child{
								border: none;
							}

							a{
								border: none;
								padding: 5px 10px;
								display: block;
								color:white;
							}
						}
						
						&.all-sports{
							background: @color-highlight;
							min-width: 800px;
							padding: 10px 10px 15px 10px;
							margin-left: -8px;
							.transition-ease-in();
							left: 8px;

							li{
								float: left;
								width: 33%;
								margin: 0;
								border: none;

								a{
									padding: 5px;
									color: @white;
								}
							}
						}

					}
				}
			}
		}
	}

	.active-nav-all{
		display: block;
	}


}
@media all and (max-width: 1180px) {

	#header-main{
		.nav-global{
			ul{
				>li{
					a{
						padding: 12px 16px 16px 16px;
					}
				} 
			}
		}
		.nav-main {
		
			ul{
				width:22%;
				padding-left: 5px;
				&.date-drop{
					display:none;
				}
				&.date-drop-min{
					width:20%;
					display:block;
					float:right;
					li{
						ul{
							position: absolute;
							width:130px;
							display:none;
							background-color: @grey6;
							top:104px;
							margin: 0;							
							z-index: 1000;
							li{
								a{
									color:#fff;
								}
							}
						}
					
						padding: 16px 0px;
						float:right;
						a{
							padding: 0 16px;
						}
						&:hover{
						background-color: @grey6;
							a{
								color: #fff;
								
							}
							ul{
								display: block;
								

							}	
						}
					}
				}
				&.nav-sport{
					max-width: 81%;
					float:left;
				}
			}
		}
	}
}

