						}
						ul{
							top: 44px;
						}
					}
				}
				>li.more{
				padding-right: 18px;
				a{
					padding: 0;
					padding-left: 18px;
				}
					ul{
						width:138px;
						padding: 5px 6px;
						margin-top: 17px;
						left:0;
					}
				}
				>li.more.highlight{
					margin-right: 5px;
					padding-left: 1px;
					padding-right: 0px;
					ul.all-sports{
						width: 500px;
						max-width:532px;
						min-width:400px;
					}
				}
			}   /* ul end */

		} /* nav-main end	 */
	}
}


