@import  "../components/variables";
@import  "../components/mixins";
@import  "quickpick";
@import  "promos";
@import  "competitions";
@import  "match";
@import  "match-live";



.market-container{
	> li{
		margin-bottom: 12px;
		
		.market-title-selector{
			
			border-bottom: 2px solid @color-secondary;

			h2{
				display: inline-block;
				width: 49.5%;
				font-size: 14px;
				font-weight: bold;
				// padding: 0 0 10px 10px;
				padding: 0 0 5px 0px;
				cursor: pointer;
			}
			p{
				display: inline-block;
				width: 49.5%;
				text-align: right;
				font-weight:500;
			}
		}
		
	}
}

/*----- SECTIONS OF RESULTS GROUPED BY EVENT -----*/

.market-event{
	background: @grey10 !important;
	padding: 5px !important;
	border: 10px solid @white;

	header{
		padding: 6px 0 0 10px;
		min-height: 50px;
		line-height: 1.4em;
		position: relative;
		
		h2{
			font-weight: bold;
			font-size: 1.3em;
		}

		p{
			font-size: 0.9em;
		}

		.time{
			position: absolute;
			width: 40px;
			height: 45px;
			top: 0;
			right: 5px;
			text-align: center;
			font-size: 0.9em;

			&:before{
				font-family: fontAwesome;
				content: "\f017";
				color: @color-highlight;
				font-size: 2em;
				display: block;
				padding: 5px 0 0 0;
			}

		}

	}

	.market{
		background: @white;
	}

	p.racing{
		background: @white;
		padding: 12px 0 12px 55px;
		font-weight: bold;
	}
}

/*----- MARKET EVENT CAROUSEL -----*/

.market-carousel-container{
	background: white; 
	overflow: hidden;
}

.market-carousel{
	width: 2000px;
	overflow: auto;

	.market-event{
		width: 420px;
		float: left;
		margin-right: 10px;
		margin-bottom: 5px !important;
		border: none;

		h2{
			padding-bottom: 0;
		}
	}
}

/*----- MARKET DISPLAY IN COLUMNS-----*/
.market-cols{

	.clearfix;
	
	.col{
		width: 33.333333333333%;
		float: left;
		padding-right: 10px;
		
		&.double{
			width: 66.666666666666%;
		}

		&:last-child{
			padding-right: 0;
		}

		&.padding{
			section{
				padding: 0 10px 10px 10px !important;
			}
		}

		> header{
			position: relative;
			padding: 8px 0 14px 10px;
			
			p{
				position: absolute;
				right: 10px;
				bottom: 14px;
				font-size: 12px;
			}

		}

		section{
			padding: 0 !important;
			margin-bottom: 10px !important;
			//font-size: 0.9em;

			&.border{
				border: 5px solid @grey10;
				header{
					padding-bottom: 0;
					height: 35px;
				}
			}

			header{
			//	padding: 8px 10px 8px 10px;
				h2{
					padding-top: 8px;
				}
			}

			.prices{
				padding-right: 5px;
				width: 100%;
				height: 40px;

				a{
					padding-top: 0.4em;
					padding-bottom: 0.5em;
				}
			}

			ul + p{
				padding: 10px;
			}

			ul{
				li:first-child{
					div{
						border-top: 1px solid @grey9;
					}
				}
			}
		}

		header + section > header{
			padding: 0 0 0 10px;
			height: 40px;
			display: table-cell;
			vertical-align: middle;

			h2{
				padding-top: 0;
				padding-bottom: 2px;
				font-size: 16px;
				color: @grey6;
			}

			p{
				font-size: 11px;
				color: @color-secondary;
			}
		}
	}
}


/*----- MARKET DATA -----*/

// Default dimensions set for h2h markets, other market types set at the bottom of this block.

.market{
	display: table;
	width: 100%;
	
	> li{
		display: table-row;
 		width: 100%;
 		position: relative;

 		&:hover{
 			background: @color-selection-hover;
			
			.time{
				border-right: 1px solid @white;
			}

 		}

 		a{
 			font-weight: 500;
 		}
 		
 		> div {
	 		display: table-cell;
			vertical-align: middle;
			padding: 5px 0;
			border-bottom: 1px solid @color-market-border;
			font-weight: 500;
			
			-webkit-transition: all 0.5s ease-in-out;
		   -moz-transition: all 0.5s ease-in-out;
		   -o-transition: all 0.5s ease-in-out;
		   transition: all 0.5s ease-in-out;
			
			&.prices:nth-child(4), &.prices:nth-child(5){
				display:table-cell;
			}
 		}
		
		div {
			
			b{
				color: @color-highlight-2;
			}

			&.time{
				border-right: 1px solid @color-market-border;
				text-align: center;
				
				.create-percentage-main-content(70px);
				
				
				& em{
					display: block;
					text-transform: uppercase;
					color: @color-highlight;
					font-weight: bold;
					font-size: 0.8em;
					line-height: 1.6em
				}

			}

			&.selection{
				.create-percentage-main-content(200px);
				padding-left: 10px;
				
				p{
					font-size: 0.8em;
					line-height: 1.6em;
				}
			}

			&.logo{
				.create-percentage-main-content(70px);
				text-align: center;
			}

			&.options{
				.create-percentage-main-content(52px);
				text-align: right;
				padding-right: 10px;
			}

			&.prices{
				
				// .create-percentage-main-content(200px); 
				
				.table {
					display: table;
					width: 100%;
					padding: 3px;
					padding-top: 1px;					
					.table-row {
						
						display:table-row;
							> div {
								display:table-cell;
								// width:30%;
								float:none;
							
							a {
								width: 96%;
								margin-left: 4%;
								
							}
							
							&:first-child{
								
								a {
									margin-left:0;
									width:100%;
								}
							}

						}
					}
				}
				
				a{
					.price(#191919, #F5FAFF, #CBDEE9);
					padding: 5px;
					// width: 30%;
					// margin-right: 1%;
					// margin-left: 2%;
					text-align: center;
					padding-top: 0;
					padding-bottom: 3px;
					width:98%;


					&:hover{
						background: @color-highlight;
						color: @white;
						border: 1px solid #2B86BB;
						
						i {
							color: @white;
						}
					}
				}
				
				.market-title {
					display: block;
					font-weight: 600;
					font-size: 79%;
					text-align: center;
					background-color: #3095CF;
					margin: 3px;
					padding: 2px;
					color: #FFF;
					border-radius: 2px 2px 0px 0px;
					
					padding-left: 0;
					padding-right: 0;

				}
	
	
			}
		}
	}

	// A or B to win
	&.ab{
		.selection{
			.create-percentage-main-content(460px);
		}
		.prices{
			.create-percentage-main-content(251px);
			a{
				width: 48%;
			}
		}
	}

	// Outright
	&.outright{
		.selection{
			width: 80%
		}

		.prices{
			width: 20%;
			
			a{
				margin: 0;
				width: 100%;
				text-align: center;
			}
		}
	}

	// Racing (Horses, greyhounds, etc)
	&.racing{
		
		li{
			&:hover{
				background: none;
			}
		}

		.identity{
			width: 50px;
			padding-left: 5px;
			
			img{
				max-width: 40px;
			}

		}
		.selection{
			width: 100%;
			font-weight: bold;
			p{
				font-weight: normal;
			}
		}
		.prices{
			width: auto;

			&:before{
				content: " ";
				width: 120px;
				background: red;
				display: block;
			}

			a{
				.price(#191919, #F5FAFF, #CBDEE9);
				width: 30%;
				text-align: center;

				&:hover{
					background: @color-highlight;
					color: @white;
				}
				
				&:first-child{
					width: 60%
				}

			}
		}
	}

	&.antepost{
		li{
			margin-bottom: 2px;
			background: @grey11;
			display: block;
			padding: 0 10px;
		}
		a{
			display: table-cell;
			vertical-align: middle;
			height: 38px;
		}
	}

	&.tote{
		.selection{
			width: 55%;
		}
		.value{
			width: 15%;
		}
		.prices{
			width: 30%;
			a{
				.price(@grey6, @grey11);
				border: 1px solid @grey9;
				width: 100%;
			}
		}
	}


	&.top-5{
	
		counter-reset: ol-counter;
		
		li{
			display: table;
			margin-bottom: 0px;
			font-size: 1em;
			position: relative;

			div{
				// height: 48px;
				border-top: none;
				// border-bottom: 1px solid @color-market-border;
			}

			&:first-child{
				div{
					border-top: none !important;
				}
			}

			.time{
				.create-percentage-main-content(70px);
				padding-left: 33px;
				
				// width: (84px / 300)*100%;
				
				font-weight: 600;
				
				&:before{
					content: counter(ol-counter);
    				counter-increment: ol-counter;
    				position: absolute;
    				display: table-cell;
    				width: 33px;
    				height: 98%;
    				background: @grey9;
    				font-size: 1.2em;
    				left: 0;
    				top: 0;
    				vertical-align: middle;
    				text-align: center;
    				line-height: 48px;
				}

				&:after{
					display: block;
					min-width: 51px;
					content: "";
				}
			}

			.selection{
				.create-percentage-main-content(200px);
				// width: (216px / 300)*100%;
				
				a{
					display: block;
					min-width: 186px;
					line-height: 16px;
				}
			}

			.prices.single{
				padding-right: 0px;
				// min-width: 300px;
				

				a{
					width: 100%;
					font-weight: bold;
					padding: 8px 11px 8px 11px;
					margin-right: 0;
					margin-left: 0;
				}
			}
		}
		

		.team{
			display: block;
			font-weight: bold;
			font-size: 1em;
				
			.score{
				float: right;
				padding-right: 10px;
			}

			&:last-child:before{
				content: "v";
				color: @black;
				padding-right: 5px; 

			}

		}
	}

	&.match-highlight{
		font-size: 12px;

		.time{
			width: (65px / 300)*100%;
		}

		.selection{
			width: (235px / 300)*100%;
			padding: 10px;
			a{
				display: block;
				min-width: 186px;
			}
		}

		.team{
			display: block;
			font-weight: bold;
			position: relative;
				
			.score{
				position: absolute;
				padding: 5px;
				right: -10px;
				background: @color-highlight;
				text-align: center;
				min-width: 25px;
			}

			&.home{
				
				margin-bottom: 3px;

				.score{
					top: -10px;
					padding-top: 10px;
				}
			}

			&.away{
				.score{
					bottom: -10px;
					padding-bottom: 10px;
				}
			}

			&:last-child:before{
				content: "v";
				color: @white;
				padding-right: 5px; 

			}

		}
	}
}

.col-2{
	-webkit-column-count: 2;
	-webkit-column-gap: 10px;
	-webkit-column-rule-color: @color-market-border;
	-webkit-column-rule-style: solid ;
	-webkit-column-rule-width: 1px;
	-moz-column-count: 2;
	-moz-column-gap: 10px;
	-moz-column-rule-style: @color-market-border;
	-moz-column-rule-color: #ccc;
	-moz-column-rule-style: solid;
	-moz-column-rule-width:  1px;
  
	width: 100%;
}



@media all and (max-width: 1600px) {

	.market{
		
		> li{
		
			div.prices:nth-child(6)  {
				display: none;
			}
		
		}
		
		
		
	}

}

@media all and (max-width: 1400px) {

		
	.market-cols{
		.col, .col.double{
			width: 100%;
			float: left;
			padding-right:0;
		
		}
	}
	
	
	.market{
		
		> li{
		
			> div.prices:nth-child(5) {
				display: none;
			}
		
		}
		
	}

}

@media all and (max-width: 1200px) {

	.market{
		
		> li{
		
			> div.prices:nth-child(7) {
				display: none;
			}
		
		}
		
	}


}


@media all and (max-width: 1024px) {

	
	
	.market{
		display: block;
		width: 100%;
		
		> li{
		
			display: block;
	 		width: 100%;
	 		border-top: 1px solid #E6E9EC;
	 		.clearfix;
	 		padding-bottom: 5px;
	 		
	 		div  {
		 		display: block;
		 		border-bottom:0;
		 		
		 		&.time {
			 		float:left;
			 		width:15%;
			 		text-align: left;
			 		padding-left: 5px;
		 		}
		 		&.selection {
			 		margin-left:15%;
			 		width:85%;
			 		.clearfix;
		 		}
		 		
		 		
		 		&.prices {
				 	width: 50%;
					margin-right: 0%;
					margin-left: 0%;
					float: left;
			 		
			 		&:after {
				 		display:table;
				 		clear:both;
				 		content:'';
			 		}
			 		a {
			 			margin-right: 0%;
						margin-left: 1%;
						width: 32%;
					
			 		}
			 		
			 		.market-title {
			 			margin: 2px;
			 		}
		 		
		 		}
		 		
		 		
		 		
		 		&.options {
		 			width:100%;
		 			padding:0;
		 			padding-top:5px;
		 			padding-bottom:5px;
		 			
		 			position: absolute;
					top: 11px;
					right: 5px;

		 		}
	 		}
	 	}
	 	
	 	
	 	// top 5 markets
	 	
	 	&.top-5 {
			font-size: 0.9em; 
			
			li {
				display:block;
				padding-bottom: 0px;
				
				div  {
			 		display: table-cell;
			 		border-bottom: 0;
			 		float:none;
			 		
			 		
			 		
			 		&.time {
				 		padding-left: 40px;
			 		}
			 		
				 	&.prices {
				 	
						display: block;
						padding: 0;
						margin-top: 0px;
						height: auto;
						margin-bottom: 8px;
						float: left;
						
						&.single {
							clear: both;
							min-width: 100%;
							float: none;
						
						}
	
				 	}
			 	
			 	}
		 	}
		}
		
 		
 	}
 	
 	

	.market-container{
		
		> li{

			.market-title-selector{
			
				p {
					margin-right: -5%;
				}
				
				h2 {
					padding: 0 0 5px 0px;
				}
			}
			
		}
	}

}



