.wrapper-width-mixin(@width) when (@width <= @breakpoint-mobile){
	.wrapper-page{
		width: @width !important;
	}
	.container-main{
		// background: red;
		max-width: 100%;

		
		#content-side-panel-1 {
			width: 100% !important;
			margin: 0;
		}	
	}

	#footer-main{
		width: 100% !important;
	}
	
}

.wrapper-width-mixin(@width) when (@width >= @breakpoint-desktop){
	.wrapper-page{
		width: 100%;
		&:extend(.centralise);
		overflow: hidden;
		padding: @wrapper-page-padding-px;
		padding-bottom: 0;
		min-height: 600px;
	}
}

.create-percentage-page-wrapper(@content-width,  @internal-padding-total: @width-internal-content-padding-total, @content-container: @width-desktop){
	width: (@content-width / (@content-container - @internal-padding-total))*100%;
}


.create-percentage-main-content(@content-width,  @internal-padding-total: @width-internal-content-padding-total, @content-container: @width-content-main-reduced-px){
	width: (@content-width / (@content-container - @internal-padding-total))*100%;
}

.create-percentage-bet-slip(@content-width, @internal-padding-total: 10px, @content-container: @width-side-panel-2-px){
	width: (@content-width / (@content-container - @internal-padding-total))*100%;
}

/* Micro ClearFix Mixin */
.clearfix(){
  zoom:1;
  &:before, &:after{ content:""; display:table; }
  &:after{ clear: both; }
}

.transition-ease-in(){
	-webkit-transition: all 0.7s ease-in-out;
	-moz-transition: all 0.7s ease-in-out;
	-o-transition: all 0.7s ease-in-out;
	transition: all 0.7s ease-in-out
}

.pill(@pill-bg: @grey10, @pill-color: @grey2, @pill-dimension: 27px){
	display: table-cell;
	height: @pill-dimension;
	width: @pill-dimension;
	text-align: center;
	border-radius: 50%;
	background: @pill-bg;
	color: @pill-color;
	vertical-align: middle;

	&:hover{
		text-decoration: none;
		background: darken(@pill-bg, 10%);
	}
}

.btn(@btn-bg: @white, @btn-border-color: @color-highlight, @btn-text-color: @color-highlight){
	border: 2px solid @btn-border-color;
	background: @btn-bg;
	color: @btn-text-color;
	padding: 2px 10px;
	font-size: 14px;
	font-weight: bold;
	line-height: 2em;
	text-align: center;
	display: block;
	cursor: pointer;
	width: 100%;
	border-radius: 2px;

	&:hover{
		background: darken(@btn-bg, 10%);
		text-decoration: none;
	}
}

.price(@price-color: @white, @price-background: @color-link, @price-border: @color-link){
	display: block;
	color: @price-color;
	background: @price-background;
	border: 1px solid @price-border;
	padding: 9px;
	width: 31.3%;
	margin-right: 5px;
	float: left;
	font-size: 12px;
	font-weight: bold;
	border-radius: 2px;
	
	

	&:hover{
		background: darken(@price-background, 20%);
		text-decoration: none;
	}

	i{
		display: inline-block;
		width: 100%;
		text-align: center;
		font-size: 70%;
		margin-bottom: 2px;
		color: #7F94A6;
		margin-top: -2px;
	}

	em{
		display: inline-block;
		text-align: center;
		width: 100%;
	}

}

.header-section{
	padding: 12px 10px;
	font-weight: bold;
	position: relative;

	.secondary{
		position: absolute;
		right: 2px;
		font-size: 0.8em;
		bottom: 10px;
	}
}

.more-options(){
	clear: both;
	padding: 10px 3px;

	a{
		display: table-row;

		&:before{
			.pill(@color-highlight, @white);
			content: "\f0c9";
			font-family: fontAwesome;
		}
	}
	
	b{
		display: table-cell;
		vertical-align: middle;
		padding: 0 5px;
		font-weight: bold;
	}
}

.header-internal-list(){
	padding: 12px 10px;
	background: @grey10;

	h3{
		color: @black;
		font-weight: bold;
		font-size: 14px;
	}
	p{
		font-size: 0.9em;
		line-height: 1.6em;
	}
}


.rotate(@rotation){
	/* Safari */
	-webkit-transform: rotate(@rotation);

	/* Firefox */
	-moz-transform: rotate(@rotation);

	/* IE */
	-ms-transform: rotate(@rotation);

	/* Opera */
	-o-transform: rotate(@rotation);
}

.bgfade( @color, @fade ){
	background-color: fadeout(@color, 100-@fade*100%);
	@rgbahex: ~`function(color, fade){var t = (Math.round(255*fade)).toString(16).toUpperCase();t=t.length==1?'0'+t:t;return '#'+t+color.substr(1)}("@{color}","@{fade}")`;
	filter: ~"progid:DXImageTransform.Microsoft.gradient( startColorstr=@{rgbahex}, endColorstr=@{rgbahex},GradientType=0 )";
}
